import React, { useEffect, useState } from 'react'
import { GoArrowUpRight } from "react-icons/go";

function Start() {
    const [rotate, setrotate] = useState("0");

  useEffect(()=>{
    window.addEventListener( "mousemove", (e)=>{
      let mouseX = e.clientX;
      let mouseY = e.clientY;

      let deltaX = mouseX - window.innerWidth / 2;
      let deltaY = mouseY - window.innerHeight  / 2;

      let rotateValue = Math.atan2(deltaY, deltaX) * (180/Math.PI);
      setrotate(rotateValue - 180);
    });
  })
  return (
    <div className='w-full min-h-[100vh] text-black bg-[#CDEA68] flex flex-col items-center justify-center relative'>
        <div className='font-semibold text-[10vw] leading-none tracking-tighter flex flex-col items-center justify-center'>
        <h1>READY</h1>
        <h1>TO START</h1>
        <h1>THE PROJECT?</h1>
        </div>
        <div className='absolute w-1/3 top-1/2 flex gap-10 left-1/2 -translate-x-1/2 -translate-y-1/2'>
                <div className='w-[15vw] h-[15vw] flex items-center justify-center rounded-full bg-zinc-100'>
                    <div className='w-[9vw] h-[9vw] rounded-full relative bg-zinc-900'>
                      <p className='absolute top-1/2 left-1/2 text-white -translate-x-[50%] -translate-y-[50%] text-xl'>Play</p>
                      <div style={{transform: `translate(-50%, -50%) rotate(${rotate}deg)`}} className='line absolute top-1/2 left-1/2 -translate-x-[50%] -translate-y-[50%] w-full h-5'>
                      <div className='w-[1.2vw] h-[1.2vw] rounded-full bg-zinc-100'></div>
                      </div>
                    </div>
                </div>
                <div className='w-[15vw] h-[15vw] rounded-full flex items-center justify-center bg-zinc-100'>
                <div className='w-[9vw] h-[9vw] rounded-full relative bg-zinc-900'>
                <p className='absolute top-1/2 left-1/2 -translate-x-[50%] text-white -translate-y-[50%] text-xl'>Play</p>
                <div style={{transform: `translate(-50%, -50%) rotate(${rotate}deg)`}} className='line absolute top-1/2 left-1/2 -translate-x-[50%] -translate-y-[50%] w-full h-5'>
                      <div className='w-[1.2vw] h-[1.2vw] rounded-full bg-zinc-100'></div>
                      </div>
                    </div>
                </div>
            </div>
            <div className='cursor-pointer button flex items-center mt-6 justify-between text-xl w-[18%] bg-black px-4 py-2 rounded-full text-white'>START THE PROJECT<div className='bg-white text-white rounded-full p-2 scale-[.3] '><GoArrowUpRight /></div></div>
            <p className='mt-4 text-lg font-thin'>OR</p>
            <div className='cursor-pointer button flex items-center mt-6 justify-between text-xl w-[18%] bg-[#CDEA68] border-[1px] border-black px-4 py-2 rounded-full text-black'>START THE PROJECT<div className='bg-black text-white rounded-full p-2 scale-[.3] '><GoArrowUpRight /></div></div>
    </div>
  )
}

export default Start